require("./styles.css");
const imageSrc = require("./xbox.png");

const _jildert_achievement_wrapper = document.createElement("div");
_jildert_achievement_wrapper.className = "_jildert_achievement_wrapper";
_jildert_achievement_wrapper.style.display = "none";

const addChildToWrapper = (name, type) => {
  const child = document.createElement(type || "div");
  child.className = name;
  _jildert_achievement_wrapper.appendChild(child);
  return child;
};

addChildToWrapper("_jildert_achievement_left");
addChildToWrapper("_jildert_achievement_right");
addChildToWrapper("_jildert_achievement_middle");
const imageElement = addChildToWrapper("_jildert_achievement_image");
imageElement.style.backgroundImage = "url(" + imageSrc + ")";

addChildToWrapper("_jildert_achievement_title", "span");
addChildToWrapper("_jildert_achievement_description", "span");

document.body.appendChild(_jildert_achievement_wrapper);

const showAchievement = (content, title) => {
  title = title || "Achievement unlocked";

  const Achievement = document.getElementsByClassName(
    "_jildert_achievement_wrapper"
  )[0];

  const clone = Achievement.cloneNode(true);

  clone.getElementsByClassName(
    "_jildert_achievement_title"
  )[0].innerText = title;

  clone.getElementsByClassName(
    "_jildert_achievement_description"
  )[0].innerText = content;

  clone.style.display = "block";
  document.body.appendChild(clone);
};

showAchievement("Jammer ouwe");

module.exports = showAchievement;
